<template>
  <div class="container my-2">
    <h1>Ruta privada!</h1>
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >{{mensaje.texto}}</b-alert>

    <!-- Formulario para editar usuarios -->
    <form @submit.prevent="editarUser(userEditar)" v-if="editar">
      <h3>Editar usuario</h3>
      <input
        type="text"
        class="form-control my-2"
        placeholder="Nombre del usuario"
        v-model="userEditar.nombre_user"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="GameTag"
        v-model="userEditar.usuario_user"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Correo electronico"
        v-model="userEditar.email"
      />
      <input
        type="password"
        class="form-control my-2"
        placeholder="Contraseña"
        v-model="userEditar.password"
      />
      <b-button class="btn-warning mx-2 my-2" type="submit">Finalizar edición</b-button>
      <b-button class="btn-danger my-2" @click="editar = false">Cancelar edición</b-button>
    </form>

    <!--Formulario para crear usuarios-->
    <form @submit="agregarUser()" v-if="!editar">
      <h3>Añadir usuario</h3>
      <input
        type="text"
        class="form-control my-2"
        placeholder="Nombre del usuario"
        v-model="newUser.nombre_user"
        required
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="NameTag"
        v-model="newUser.usuario_user"
        required
      />
      <input
        type="email"
        class="form-control my-2"
        placeholder="Correo electronico"
        v-model="newUser.email"
        required
      />
      <input
        type="password"
        class="form-control my-2"
        placeholder="Contraseña"
        v-model="newUser.password"
        required
      />
      <input
        type="password"
        class="form-control my-2"
        placeholder="Confirmar contraseña"
        v-model="newUser.password"
        required
      />
      <b-button class="btn-success my-2" type="submit">Agregar Usuario</b-button>
    </form>
    <table class="table">
      <thead>
        <tr>
          <th scope="col">Nombre Usuario</th>
          <th scope="col">NameTag</th>
          <th scope="col">Correo</th>
          <th scope="col">Rol</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in user" :key="index">
          <td>{{item.nombre_user}}</td>
          <td>{{item.usuario_user}}</td>
          <td>{{item.email}}</td>
          <td>{{item.role}}</td>
          <td>
            <b-button @click="eliminarUser(item._id)" class="btn-danger btn-sm mx-2">Eliminar</b-button>
            <b-button @click="activarFormEdicion(item._id)" class="btn-warning btn-sm">Editar</b-button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "User",
  data() {
    return {
      //Este array contiene toda la informacio de los usuarios
      user: [],
      dismissSecs: 3,
      dismissCountDown: 0,
      mensaje: { color: "", texto: "" },
      newUser: {
        nombre_user: "",
        usuario_user: "",
        password: "",
        email: "",
        role: ""
      },
      editar: false,
      userEditar: {}
    };
  },
  computed: {
    ...mapState(["token"])
  },

  //Lista todos los usuarios en el CRUD
  created() {
    this.listarUser();
  },
  methods: {
    //axios llama al backend desde la ruta que pasamos por default para comsumir la API
    listarUser() {
      this.axios
        .get("/user")
        .then(res => {
          //Le pasamos a nuestro array la informacion que esta en la API
          this.user = res.data;
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    //Crea usuario
    agregarUser() {
      const config = {
        headers: {
          token: this.token
        }
      };
      this.axios
        .post("/nuevoUser", this.newUser, config)
        .then(res => {
          this.user.push(res.data);
          (this.user.nombre_user = ""),
            (this.user.usuario_user = ""),
            (this.user.password = ""),
            (this.user.email = ""),
            (this.user.role = ""((this.mensaje.color = "success"))),
            (this.mensaje.texto = "Usuario creado"),
            this.showAlert();
        })
        .catch(e => {
          console.log(e.response);
        });
    },

    eliminarUser(id) {
      //Ruta usada desde nuestra api
      this.axios
        .delete(`/user/${id}`)
        .then(res => {
          const index = this.user.findIndex(item => item._id === res.data._id);
          //Indicamos que solo vamos a eliminar un solo usuario y permite que la pagina no se recargue una vez se haya eliminado
          this.user.splice(index, 1);
          (this.mensaje.color = "danger"),
            (this.mensaje.texto = "Usuario eliminado"),
            this.showAlert();
        })
        .catch(e => {
          console.log(e.response);
        });
    },

    activarFormEdicion(id) {
      this.editar = true;
      this.axios
        .get(`/user/${id}`)
        .then(res => {
          this.userEditar = res.data;
        })
        .catch(e => {
          console.log(e.response);
        });
    },

    editarUser(item) {
      const config = {
        headers: {
          token: this.token
        }
      };
      //${id} indica que nuestro id es dinamico
      this.axios
        .put(`/user/${item._id}`, item, config)
        .then(res => {
          const index = this.user.findIndex(n => n._id === res.data._id);
          this.user[index].nombre_user = res.data.nombre_user;
          this.user[index].usuario_user = res.data.usuario_user;
          this.user[index].password = res.data.password;
          this.user[index].email = res.data.email;
          (this.mensaje.color = "primary"),
            (this.mensaje.texto = "Usuario Actualizado"),
            this.showAlert();
          this.editar = false;
        })
        .catch(e => {
          console.log(e.response);
          (this.mensaje.color = "danger"),
            (this.mensaje.texto = "Ops algo salio mal!"),
            this.showAlert();
        });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    }
  }
};
</script>
